import {Component, OnInit} from '@angular/core';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Collaborateur} from '../../model';
import {Router} from '@angular/router';
import {sanitize} from '../../utils';

@Component({
    selector: 'app-collaborateurs',
    templateUrl: './collaborateurs.component.html',
    styleUrls: ['./collaborateurs.component.scss']
})
export class CollaborateursComponent implements OnInit {
    rows: Array<Collaborateur> = [];
    datas: Array<Collaborateur> = [];


    constructor(private pouchdbservice: PouchdbService, private router: Router) {

    }

    ngOnInit() {
        this.pouchdbservice.getCollaborateurs().subscribe((data) => {
            this.datas = data.sort((x, y) => (x.nom + " " + x.prenom).localeCompare(y.nom + " " + y.prenom) );
            this.rows = this.datas;
        });
    }
    
    updateFilter(event) {
        let val = event.target.value;
        if (!val) {
            this.rows = this.datas;
            return;
        };
        val = sanitize(val);
        const temp = this.datas.filter(function (d) {
            return sanitize(d.nom).indexOf(val) !== -1
                || (d.adresse && sanitize(d.adresse.ville).indexOf(val) !== -1)
                || sanitize(d.prenom).indexOf(val) !== -1;
        });
        this.rows = temp;
    }

    onSelect(_id: string) {
        this.router.navigate(['/collaborateurs/' + _id]);
    }

}



