import {Pipe, PipeTransform} from '@angular/core';
import {LienOrganisme} from '../model';

@Pipe({
    name: 'liensOrganismeVille'
})
export class LiensOrganismeVillePipe implements PipeTransform {

    transform(liens: Array<LienOrganisme>, args?: any): any {
        if (liens) {
            return liens.map((lien) => {
                if(lien.organisme.adresse){
                    return lien.organisme.adresse.ville + " (" + lien.organisme.adresse.codePostal+")";
                }
            }).join("<br/>");
        }
    }

}



