import {Component, OnInit, Input} from '@angular/core';
import {Organisme, Adresse} from '../../model';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {DomSanitizer, SafeUrl} from '@angular/platform-browser';

@Component({
    selector: 'app-organisme-card',
    templateUrl: './organisme-card.component.html',
    styleUrls: ['./organisme-card.component.scss']
})
export class OrganismeCardComponent {
    image: SafeUrl;

    _organisme: Organisme = new Organisme();

    @Input() set organisme(value: Organisme) {
        this._organisme = value;
        if (!this._organisme.adresse){
            this._organisme.adresse = new Adresse();
        }
        this.loadImage();

    }

    constructor(private pouchdbservice: PouchdbService, private sanitizer: DomSanitizer) {}

    loadImage() {
        if (this._organisme && this._organisme._id) {
            this.pouchdbservice.getImage(this._organisme._id).subscribe(
                response => {
                    if (response) {
                        this.image = this.sanitizer.bypassSecurityTrustResourceUrl(response);
                    }
                },
                err => {
                    console.log(err);
                }
            );
        }
    }

}



