import {Pipe, PipeTransform} from '@angular/core';
import {LienOrganisme} from '../model';

@Pipe({
    name: 'liensOrganisme'
})
export class LiensOrganismePipe implements PipeTransform {

    transform(liens: Array<LienOrganisme>, args?: any): any {
        if (liens) {
            return liens.map(lien => lien.organisme.nom).join("<br/>");
        }
    }

}



