import {Component, OnInit, ViewChild} from '@angular/core';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {Organisme, Location, Adresse} from '../../model';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';
import {} from 'googlemaps';
import {GooglePlaceService} from '../../services/google-place-service/google-place.service';
import * as _ from 'lodash';

@Component({
    selector: 'app-organisme',
    templateUrl: './organisme.component.html',
    styleUrls: ['./organisme.component.scss']
})
export class OrganismeComponent implements OnInit {
    @ViewChild('fileInput') fileInput;
    form: FormGroup;
    private formSubmitAttempt: boolean = false;
    errorMessage: String;
    editForm = false;
    createForm = false;
    organisme: Organisme = new Organisme();
    typeOrganisme: Array<string> = [];
    private place: google.maps.places.PlaceResult

    constructor(private fb: FormBuilder, private route: ActivatedRoute,
        private pouchdbservice: PouchdbService,
        private router: Router,
        private googlePlaceService: GooglePlaceService
    ) {
        this.form = this.fb.group({
            nom: ['', Validators.required],
            telephone: [''],
            web: [''],
            typeOrganisme: [''],
            adresse: this.fb.group({
                adresseComplete: '',
                complement: '',
                cedex: '',
                rue: '',
                numero: '',
                boitePostale: '',
                residence: '',
                ville: '',
                codePostal: '',
                location: this.fb.group({
                    latitude: '',
                    longitude: '',
                })
            }),
        });
    }

    ngOnInit() {
        this.pouchdbservice.getParametres().subscribe(
            response => {
                this.typeOrganisme = response.listesDeChoix.typeOrganisme;
            }
        );
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.editForm = false;
                    this.createForm = true;
                } else {
                    this.loadData(id);
                }
            });
    }

    loadData(id: string) {
        this.pouchdbservice.get(id).subscribe(
            response => {
                this.organisme = response;
            },
            err => {
                this.errorMessage = err;
            }
        );

    }

    toggleEdit() {
        this.editForm = !this.editForm;
        if (this.editForm) {
            this.form.patchValue(this.organisme);
        }
    }

    isFieldInvalid(field: string) {
        return (
            this.form.get(field).invalid &&
            (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt))
        );
    }

    setAddressOnChange(place: google.maps.places.PlaceResult) {
        this.place = place;
        if (this.place) {
            //set latitude, longitude and zoom
            var adresse: Adresse = new Adresse();
            adresse.location = new Location();
            adresse.location.latitude = this.googlePlaceService.latitude(this.place);
            adresse.location.longitude = this.googlePlaceService.longitude(this.place);
            adresse.numero = this.googlePlaceService.street_number(this.place);
            adresse.rue = this.googlePlaceService.street(this.place);
            adresse.codePostal = this.googlePlaceService.postal_code(this.place);
            adresse.ville = this.googlePlaceService.city(this.place);
            adresse.boitePostale = this.googlePlaceService.post_box(this.place);
            adresse.residence = this.googlePlaceService.establishment(this.place);
            adresse.complement = this.googlePlaceService.floor(this.place);
            adresse.ville = this.googlePlaceService.city(this.place);
            adresse['adresseComplete'] = null;
            this.form.get('adresse').patchValue(adresse);
        } else {
            this.form.get('adresse').get('location').setValue(new Location());
        }
    }

    onSubmit() {
        if (this.form.valid) {
            this.organisme = _.merge(this.organisme, this.form.value);

            this.pouchdbservice.save(this.organisme).subscribe(
                response => {
                    console.log(response);
                    this.addImage(response.id, response.rev);
                },
                err => {
                    this.errorMessage = err;
                }
            );

        }
        this.formSubmitAttempt = true;
    }

    addImage(id: string, rev: string) {
        let fileBrowser = <HTMLInputElement> this.fileInput.nativeElement;
        if (fileBrowser.files && fileBrowser.files[0]) {
            let f: File = fileBrowser.files[0];
            let contentType = f.type;
            let reader = new FileReader();
            let self = this;
            reader.onload = function (e) {
                let target: any = e.target;
                let data: string = target.result;
                data = data.replace("data:" + contentType + ";base64,", "");
                self.pouchdbservice.addImage(id, rev, contentType, data).subscribe(
                    response => {
                        console.log(response);
                        self.swithToShowMode(id)
                    },
                    err => {
                        self.errorMessage = err;
                    }
                );
            };
            reader.readAsDataURL(f);

        }
        else {
            this.swithToShowMode(id)
        }
    }

    swithToShowMode(id) {
        if (this.createForm) {
            this.createForm = false;
            this.editForm = false;
            this.router.navigate(['/organismes/' + id]);
        } else {
            this.createForm = false;
            this.editForm = false;
            this.loadData(id);
        };
    }

}



