import {Component, OnInit} from '@angular/core';
import {Prospection, Action, Collaborateur, Contact, Organisme, Parametres} from '../../model';
import {FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-prospection',
    templateUrl: './prospection.component.html',
    styleUrls: ['./prospection.component.scss']
})
export class ProspectionComponent implements OnInit {
    form: FormGroup;
    private formSubmitAttempt: boolean = false;
    errorMessage: string;
    editForm = false;
    createForm = false;
    prospection: Prospection = new Prospection();
    collaborateurs: Array<Collaborateur> = [];
    contacts: Array<Contact> = [];
    organismes: Array<Organisme> = [];
    parametres: Parametres = new Parametres();
    actions: FormArray;

    constructor(private fb: FormBuilder, private route: ActivatedRoute, private pouchdbservice: PouchdbService, private router: Router) {
        this.form = this.fb.group({
            nom: ['', Validators.required],
            contact: this.fb.group({
                _id: ['', Validators.required],
                nom: ['']
            }),
            client: this.fb.group({
                _id: [''],
                nom: ['']
            }),
            dateContact: ['', Validators.required],
            modaliteObtention: [''],
            cible: [''],
            thematique: [''],
            modaliteIS: [''],
            commentaire: [''],
            dropboxRepertoire: [''],
            montantProposition: [''],
            nbJours: [''],
            statut: [''],
            actions: this.fb.array([]),
        });
        this.actions = this.form.get('actions') as FormArray;
    }

    ngOnInit() {
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.editForm = false;
                    this.createForm = true;
                } else {
                    this.loadProspection(id);
                }
            });
        this.pouchdbservice.getCollaborateurs().subscribe(
            data => {
                this.collaborateurs = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getOrganismes().subscribe(
            data => {
                this.organismes = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getContacts().subscribe(
            data => {
                this.contacts = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    loadProspection(id: string) {
        this.pouchdbservice.get(id).subscribe(
            data => {
                this.prospection = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    toggleEdit() {
        this.editForm = !this.editForm;
        if (this.editForm) {
            this.form.reset();
            this.actions.controls = [];
            if (this.prospection.actions) {
                for (let lien of this.prospection.actions) {
                    this.actions.push(this.createAction(lien));
                }
            }
            this.form.patchValue(this.prospection);
        }
    }

    isFieldInvalid(field: string) {
        return this.form.get(field).invalid && (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt));
    }
    isFieldInvalidInActions(index:number, field: string) {
        let control = this.actions.controls[index].get(field);
        return control.invalid && (control.touched || (control.untouched && this.formSubmitAttempt));
    }


    onSubmit() {
        if (this.form.valid) {
            this.prospection = Object.assign(this.prospection, this.form.getRawValue());
            if (this.prospection.contact && this.prospection.contact._id) {
                let contact : Contact = this.contacts.find(org => org._id === this.prospection.contact._id);
                this.prospection.contact.nom = contact.nom;
                this.prospection.contact.prenom = contact.prenom;
            }
            if (this.prospection.client && this.prospection.client._id) {
                this.prospection.client.nom = this.organismes.find(org => org._id === this.prospection.client._id).nom;
            }
            if (this.prospection.actions) {
                for (let lien of this.prospection.actions) {
                    if (lien.collaborateur && lien.collaborateur._id) {
                        let collaborateur = this.collaborateurs.find(org => org._id === lien.collaborateur._id);
                        lien.collaborateur.nom = collaborateur.nom;
                        lien.collaborateur.prenom = collaborateur.prenom;
                    }
                }
            }
           
            this.pouchdbservice.save(this.prospection).subscribe(
                response => {
                    console.log(response);
                    this.swithToShowMode(response.id)
                },
                err => {
                    this.errorMessage = err;
                }
            );

        }
        this.formSubmitAttempt = true;
    }


    swithToShowMode(id) {
        if (this.createForm) {
            this.createForm = false;
            this.editForm = false;
            this.router.navigate(['/prospections/' + id]);
        } else {
            this.createForm = false;
            this.editForm = false;
            this.loadProspection(id);
        };
    }

    createAction(lien: Action): FormGroup {
        return this.fb.group({
            date: [lien.date, Validators.required],
            typeAction: [lien.typeAction, Validators.required],
            collaborateur: this.fb.group({
                _id: [lien.collaborateur._id, Validators.required]
            })
        });
    }

    addAction() {
        this.actions.push(this.createAction(new Action()));
    }

    removeAction(index: number) {
        this.actions.removeAt(index);
    }


}


