import {Directive, ElementRef, Input, Output, EventEmitter} from '@angular/core';
import {NgControl} from "@angular/forms";
import {} from 'googlemaps';
import {MapsAPILoader} from '@agm/core';


@Directive({selector: '[appGooglePlace]'})
export class GooglePlaceDirective {
    @Output() setAddress: EventEmitter<google.maps.places.PlaceResult> = new EventEmitter();


    constructor(private el: ElementRef, private control: NgControl, private mapsAPILoader: MapsAPILoader) {

    }

    ngAfterContentInit() {
        this.mapsAPILoader.load().then(() => {
            const autocomplete = new google.maps.places.Autocomplete(this.el.nativeElement, {
                componentRestrictions: {country: "fr"}
            });
            google.maps.event.addListener(autocomplete, "place_changed", () => {

                //get the place result
                let place = autocomplete.getPlace();

                //verify result
                if (place.geometry === undefined || place.geometry === null) {
                    place = null;
                }
                this.control.control.setValue(this.el.nativeElement.value);
                this.setAddress.emit(place);
            });
        });
    }
}



