import {Component, Input, OnChanges} from '@angular/core';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';

@Component({
    selector: 'db-sync-indicator',
    templateUrl: './db-sync-indicator.component.html'
})
export class DbSyncIndicatorComponent {

    syncText: string;
    syncStyle = {'color': 'red'};

    syncStatus: boolean;
    serverUp: boolean;

    constructor(private pouchdbservice: PouchdbService) {

    }

    ngOnInit() {
        this.pouchdbservice.syncStatus.subscribe(result => {
            this.syncStatus = result;
            this.refresh();
        });
        this.pouchdbservice.couchdbUp.subscribe(result => {
            this.serverUp = result;
            this.refresh();
        });
    }

    refresh() {
        if (!this.serverUp) {
            this.syncStyle.color = 'red';
            this.syncText = 'Offline';
        } else if (!this.syncStatus) {
            this.syncStyle.color = 'blue';
            this.syncText = 'Trying to Sync.(Couch is up but sync is down)';
        } else {
            this.syncStyle.color = 'green';
            this.syncText = 'Synchronised';
        }
    }

}



