import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/observable/from';
// the pouchdb-adapter file/class in the same folder
import {PouchDbAdapter} from './pouchdb-adapter';
import {AuthService} from '../auth/auth.service';
import {Organisme, Contact, Acknowledge, Parametres, Mission, Collaborateur, Prospection} from '../../model';
import {HttpClient} from '@angular/common/http';
import * as _ from 'lodash';

const REMOTE_COUCH_DB_ADDRESS = 'https://login:password@104.155.22.221/ellyx';
//const REMOTE_COUCH_DB_ADDRESS = 'http://login:password@104.155.22.221:5984/ellyx';
//const REMOTE_COUCH_DB_ADDRESS = 'http://admin:password@localhost:5984/ellyx';

@Injectable()
export class PouchdbService {

    // handler for the adapter class
    private _pouchDbAdapter: PouchDbAdapter;

    private _parametres: Parametres;

    // rxjs observables to broadcast sync status
    syncStatus: Observable<boolean>;
    couchdbUp: Observable<boolean>;

    // URL of CouchDB (hardwired above)
    remoteCouchDBAddress: string = REMOTE_COUCH_DB_ADDRESS;

    // initiate adapter class and hook up the observables
    constructor(private http: HttpClient, private authService: AuthService) {
        this.authService.isLoggedIn.subscribe(isLogged => {
            if (isLogged){
                this.initConnexion(authService.user.login, authService.user.password);
            } else {
                this.closeConnexion();
            }
        });

    }

    initConnexion(login: string, password: string) {
        this._pouchDbAdapter = new PouchDbAdapter(this.authService, REMOTE_COUCH_DB_ADDRESS.replace("login", login).replace("password", password));
        this.syncStatus = this._pouchDbAdapter.syncStatus.asObservable();
        this.couchdbUp = this._pouchDbAdapter.couchDbUp.asObservable();
        this.initDb();
    }
    
    closeConnexion() {
        this._pouchDbAdapter = null;
    }



    postDoc(doc): Promise<any> {
        return Promise.resolve(this._pouchDbAdapter.put(doc));
    }

    getCollaborateurs(): Observable<Array<Collaborateur>> {
        return this._pouchDbAdapter.getDocsOfType(Collaborateur.name);
    }

    getMissions(): Observable<Array<Mission>> {
        return this._pouchDbAdapter.getDocsOfType(Mission.name);
    }

    getProspections(): Observable<Array<Prospection>> {
        return this._pouchDbAdapter.getDocsOfType(Prospection.name);
    }

    getOrganismes(): Observable<Array<Organisme>> {
        return this._pouchDbAdapter.getDocsOfType(Organisme.name);
    }

    getContacts(): Observable<Array<Contact>> {
        return this._pouchDbAdapter.getDocsOfType(Contact.name);
    }

    get(docId: string): Observable<any> {
        return this._pouchDbAdapter.get(docId);
    }

    save(doc: any): Observable<Acknowledge> {
        if (doc._id === Parametres.name) {
            this._parametres = null;
        }
        if (!!doc._id) {
            return this._pouchDbAdapter.put(doc);
        } else {
            return this._pouchDbAdapter.post(doc);
        }
    }

    getParametres(): Observable<Parametres> {
        return new Observable<Parametres>(observer => {
            if (this._parametres) {
                observer.next(this._parametres)
            } else {
                this._pouchDbAdapter.get(Parametres.name).subscribe(
                    (data) => {
                        this._parametres = data;
                        observer.next(this._parametres)
                    },
                    err => observer.error(err));
            }

        });
    }



    getImage(docId: string): Observable<string> {
        return this._pouchDbAdapter.getAttachment(docId, 'image');
    }

    addImage(docId: string, docRev: string, contentType: string, myBase64String: string): Observable<Acknowledge> {
        return this._pouchDbAdapter.putAttachment(docId, docRev, 'image', myBase64String, contentType);
    }


    private initDb() {

        //        this._pouchDbAdapter.create        Index({
        //            i        ndex: {
        //                fields: ['name', '        type'],
        //                name: 'type        Index',
        //                ddoc: 'typeDesi        gndoc',
        //                type:         'json'
        //                    }
        //        }).then(function (re        sult) {
        //            console.log("index cre        ated");
        //        }).catch(function         (err) {
        //            console.lo        g(err);
        //        });

//        this.getOrganismes().subscribe((data) => {
//            if (data.length < 4) {
//                this.http.get('./assets/data/organisme.json')
//                    .subscribe(data => {
//                        var organismes: Array<Organisme> = data as Array<Organisme>;
//                        for (let organisme of organismes) {
//                            organisme.type = Organisme.name;
//                            this.save(organisme);
//                        }
//                    });
//            }
//        });
//
//        this.getContacts().subscribe((data) => {
//            if (data.length < 4) {
//                this.http.get('./assets/data/contact.json')
//                    .subscribe(data => {
//                        var contacts: Array<Contact> = data as Array<Contact>;
//                        for (let contact of contacts) {
//                            contact.type = Contact.name;
//                            this.save(contact);
//                        }
//                    });
//            }
//        });


        this.getParametres().subscribe(
            data => {
                var parametres: Parametres = new Parametres();
                if (Object.keys(data.listesDeChoix).length != Object.keys(parametres.listesDeChoix).length) {
                    parametres = _.merge(parametres, data);
                    parametres.listesDeChoix = Object.assign(parametres.listesDeChoix, data.listesDeChoix);
                    this.save(parametres);
                    console.log("parameters already exist but new variable in 'listeDeChoix'");
                    return;
                }
                console.log("parameters already exist" + data)
            },
            err => {
                this.http.get('./assets/data/parametres.json')
                    .subscribe(data => {
                        var _parametres = data as Parametres;
                        var parametres: Parametres = new Parametres();
                        parametres = _.merge(parametres, _parametres);
                        parametres.listesDeChoix = Object.assign(parametres.listesDeChoix, _parametres.listesDeChoix);
                        this.save(parametres);
                    });


            });
    }

}





