import {Injectable} from '@angular/core';
import {} from 'googlemaps';

@Injectable()
export class GooglePlaceService {

    constructor() {}

    private find(address_components: google.maps.GeocoderAddressComponent[], query: string, val: string = null) {

        for (let attr of address_components) {
            for (let type of attr.types) {
                if (type === query) {
                    return val ? attr[val] : attr;
                }
            }
        }
        return null;
    }
    
    latitude(place: google.maps.places.PlaceResult) {
        return place.geometry.location.lat();
    }
    longitude(place: google.maps.places.PlaceResult) {
        return place.geometry.location.lng();
    }

    street_number(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'street_number', 'long_name');
    }

    street(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'route', 'long_name');
    }

    city(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'locality', 'long_name');
    }

    state(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'administrative_area_level_1', 'long_name');
    }

    postal_code(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'postal_code', 'long_name');
    }

    country(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'country', 'long_name');
    }

    administrative_area_level_2(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'administrative_area_level_2', 'long_name');
    }


    // MORE NOT USED YET /////
    intersection(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'intersection');
    }

    political(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'political');
    }

    administrative_area_level_3(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'administrative_area_level_3');
    }

    administrative_area_level_4(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'administrative_area_level_4');
    }

    administrative_area_level_5(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'administrative_area_level_5');
    }

    colloquial_area(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'colloquial_area');
    }

    ward(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'ward');
    }

    sublocality(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'sublocality');
    }

    sublocality_level_1(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'sublocality_level_1');
    }

    sublocality_level_2(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'sublocality_level_2');
    }

    sublocality_level_3(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'sublocality_level_3');
    }

    sublocality_level_4(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'sublocality_level_4');
    }

    sublocality_level_5(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'sublocality_level_5');
    }

    neighborhood(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'neighborhood');
    }

    premise(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'premise');
    }

    subpremise(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'subpremise');
    }

    natural_feature(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'natural_feature');
    }

    airport(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'airport');
    }

    park(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'park');
    }

    point_of_interest(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'point_of_interest');
    }

    floor(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'floor');
    }

    establishment(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'establishment');
    }

    parking(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'parking');
    }

    post_box(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'post_box');
    }

    postal_town(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'postal_town');
    }

    room(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'room');
    }

    bus_station(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'bus_station');
    }

    train_station(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'train_station');
    }

    transit_station(place: google.maps.places.PlaceResult) {
        return this.find(place.address_components, 'transit_station');
    }


}




