import {Component, OnInit} from '@angular/core';
import {Mission, LienMissionCollaborateur, LienEntreMission, Collaborateur, Contact, Organisme, Parametres} from '../../model';
import {FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import {ActivatedRoute} from "@angular/router";
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-mission',
    templateUrl: './mission.component.html',
    styleUrls: ['./mission.component.scss']
})
export class MissionComponent implements OnInit {
    form: FormGroup;
    private formSubmitAttempt: boolean = false;
    errorMessage: string;
    editForm = false;
    createForm = false;
    mission: Mission = new Mission();
    collaborateurs: Array<Collaborateur> = [];
    contacts: Array<Contact> = [];
    organismes: Array<Organisme> = [];
    parametres: Parametres = new Parametres();
    missions: Array<Mission> = [];
    liensCollaborateur: FormArray;
    liensMission: FormArray;

    constructor(private fb: FormBuilder, private route: ActivatedRoute, private pouchdbservice: PouchdbService, private router: Router) {
        this.form = this.fb.group({
            nom: ['', Validators.required],
            numFacture: [''],
            contact: this.fb.group({
                _id: [''],
                nom: ['']
            }),
            client: this.fb.group({
                _id: [''],
                nom: ['']
            }),
            financeur: this.fb.group({
                _id: [''],
                nom: ['']
            }),
            cdt: [''],
            programme: [''],
            dateDebut: ['', Validators.required],
            dateFin: ['', Validators.required],
            nbJours: [{value: '', disabled: true}],
            missionType: [''],
            missionFormat: [''],
            modaliteObtention: [''],
            cible: [''],
            thematique: [''],
            modaliteIS: [''],
            commentaire: [''],
            restrictionAcces: [''],
            avancement: [''],
            dropboxRepertoire: [''],
            liensCollaborateur: this.fb.array([]),
            liensMission: this.fb.array([])
        });
        this.liensCollaborateur = this.form.get('liensCollaborateur') as FormArray;
        this.liensMission = this.form.get('liensMission') as FormArray;

        this.form.get('dateDebut').valueChanges.subscribe(data => {
            this.calculateNbJours();
        });
        this.form.get('dateFin').valueChanges.subscribe(data => {
            this.calculateNbJours();
        });
    }

    ngOnInit() {
        this.route.params.subscribe(
            params => {
                var id = params['id'];
                if (id == '0') {
                    this.editForm = false;
                    this.createForm = true;
                } else {
                    this.loadMission(id);
                }
            });
        this.pouchdbservice.getCollaborateurs().subscribe(
            data => {
                this.collaborateurs = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getOrganismes().subscribe(
            data => {
                this.organismes = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getContacts().subscribe(
            data => {
                this.contacts = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getMissions().subscribe(
            data => {
                this.missions = data.sort((x, y) => x.nom.localeCompare(y.nom));
            },
            err => {
                this.errorMessage = err;
            }
        );
        this.pouchdbservice.getParametres().subscribe(
            data => {
                this.parametres = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    loadMission(id: string) {
        this.pouchdbservice.get(id).subscribe(
            data => {
                this.mission = data;
            },
            err => {
                this.errorMessage = err;
            }
        );
    }

    toggleEdit() {
        this.editForm = !this.editForm;
        if (this.editForm) {
            this.form.reset();
            this.liensCollaborateur.controls = [];
            if (this.mission.liensCollaborateur) {
                for (let lien of this.mission.liensCollaborateur) {
                    this.liensCollaborateur.push(this.createLienCollaborateur(lien));
                }
            }
            this.liensMission.controls = [];
            if (this.mission.liensMissions) {
                for (let lien of this.mission.liensMissions) {
                    this.liensMission.push(this.createLienMission(lien));
                }
            }
            this.form.patchValue(this.mission);
        }
    }

    isFieldInvalid(field: string) {
        return (
            this.form.get(field).invalid &&
            (this.form.get(field).touched || (this.form.get(field).untouched && this.formSubmitAttempt))
        );
    }
    
    isFieldInvalidInLienCollaborateur(index:number, field: string) {
        let control = this.liensCollaborateur.controls[index].get(field);
        return control.invalid && (control.touched || (control.untouched && this.formSubmitAttempt));
    }

    onSubmit() {
        if (this.form.valid) {
            this.mission = Object.assign(this.mission, this.form.getRawValue());
            if (this.mission.contact && this.mission.contact._id) {
                let contact: Contact = this.contacts.find(org => org._id === this.mission.contact._id);
                this.mission.contact.nom = contact.nom;
                this.mission.contact.prenom = contact.prenom;
            }
            if (this.mission.client && this.mission.client._id) {
                this.mission.client.nom = this.organismes.find(org => org._id === this.mission.client._id).nom;
            }
            if (this.mission.financeur && this.mission.financeur._id) {
                this.mission.financeur.nom = this.organismes.find(org => org._id === this.mission.financeur._id).nom;
            }
            if (this.mission.liensCollaborateur) {
                for (let lien of this.mission.liensCollaborateur) {
                    if (lien.collaborateur && lien.collaborateur._id) {
                        let collaborateur: Collaborateur = this.collaborateurs.find(org => org._id === lien.collaborateur._id);
                        lien.collaborateur.nom = collaborateur.nom;
                        lien.collaborateur.prenom = collaborateur.prenom;
                    }
                }
            }
            if (this.mission.liensMissions) {
                for (let lien of this.mission.liensMissions) {
                    if (lien.mission && lien.mission._id) {
                        lien.mission.nom = this.collaborateurs.find(org => org._id === lien.mission._id).nom;
                    }
                }
            }
            if (this.mission.restrictionAcces) {
                let restrictionAcces: Array<Collaborateur> = this.form.get('restrictionAcces').value;
                this.mission.restrictionAcces = [];
                for (let coll of restrictionAcces) {
                    let newColl = new Collaborateur();
                    newColl._id = coll._id;
                    newColl.nom = coll.nom;
                    newColl.prenom = coll.prenom;
                    this.mission.restrictionAcces.push(newColl);
                }
            }
            this.pouchdbservice.save(this.mission).subscribe(
                response => {
                    console.log(response);
                    this.swithToShowMode(response.id)
                },
                err => {
                    this.errorMessage = err;
                }
            );

        }
        this.formSubmitAttempt = true;
    }


    swithToShowMode(id) {
        if (this.createForm) {
            this.createForm = false;
            this.editForm = false;
            this.router.navigate(['/missions/' + id]);
        } else {
            this.createForm = false;
            this.editForm = false;
            this.loadMission(id);
        };
    }

    createLienCollaborateur(lien: LienMissionCollaborateur): FormGroup {
        return this.fb.group({
            role: [lien.role, Validators.required],
            collaborateur: this.fb.group({
                _id: [lien.collaborateur._id, Validators.required]
            })
        });
    }

    addLienCollaborateur() {
        this.liensCollaborateur.push(this.createLienCollaborateur(new LienMissionCollaborateur()));
    }

    removeLienCollaborateur(index: number) {
        this.liensCollaborateur.removeAt(index);
    }

    createLienMission(lien: LienEntreMission): FormGroup {
        return this.fb.group({
            typeLien: [lien.typeLien],
            mission: this.fb.group({
                _id: lien.mission._id
            })
        });
    }

    addLienMission() {
        this.liensMission.push(this.createLienMission(new LienEntreMission()));
    }

    removeLienMission(index: number) {
        this.liensMission.removeAt(index);
    }

    calculateNbJours() {
        let dateDebut = this.form.get('dateDebut').value;
        let dateFin = this.form.get('dateFin').value;
        if (dateDebut && dateFin) {
            this.form.get('nbJours').setValue(Math.round((new Date(dateFin).getTime() - new Date(dateDebut).getTime()) / (1000 * 60 * 60 * 24)));
        }
    }

    compare(val1, val2) {
        if(!val1 || !val2){
            return false;
        }
        return val1._id === val2._id;
    }

}


