import {Component, OnInit, ViewChild} from '@angular/core';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {Prospection} from '../../model';
import {sanitize} from '../../utils';
import {Router} from '@angular/router';
import {LiensOrganismePipe} from '../../pipes/liens-organisme.pipe';

@Component({
    selector: 'app-prospections',
    templateUrl: './prospections.component.html',
    styleUrls: ['./prospections.component.scss']
})
export class ProspectionsComponent implements OnInit {
    rows: Array<Prospection> = [];
    datas: Array<Prospection> = [];
    loadingIndicator: boolean = true;
    selected = [];
    yearFilter = [];
    currentYearFilter = new Date().getFullYear();
    currentTextFilter: string = "";
    @ViewChild('table') table: any;



    columns: any[] = [
        {prop: 'nom', flexGrow: 1},
        {prop: 'contact.nom', name: 'Contact', flexGrow: 1},
        {prop: 'client.nom', name: 'Client', flexGrow: 1},
        {prop: 'statut', name: 'Statut', flexGrow: 1},
    ];

    constructor(private pouchdbservice: PouchdbService, private router: Router) {
        for (let i = 2016; i <= this.currentYearFilter; i++) {
            this.yearFilter.push(i);
        }
    }

    ngOnInit() {
        this.pouchdbservice.getProspections().subscribe((data) => {
            this.datas = data;
            this.filter();
            setTimeout(() => {this.loadingIndicator = false;}, 1500);
        });
    }



    updateFilter(value) {
        this.currentTextFilter = sanitize(value);
        this.filter();
    }

    updateFilterYear(year) {
        this.currentYearFilter = year;
        this.filter();
    }

    filter() {
        let self = this;
        var from = new Date(this.currentYearFilter, 0, 1).getTime(); 
        var to = new Date(this.currentYearFilter, 11, 31).getTime();
        let tempData = this.datas.filter(function (d) {
            return new Date(d.dateContact).getTime() >= from && (!d.dateClotute || new Date(d.dateClotute).getTime()<=to);
        });

        if (!this.currentTextFilter) {
            this.rows = this.datas;
        } else {
            tempData = tempData.filter(function (d) {
                return sanitize(d.nom).indexOf(self.currentTextFilter) !== -1
                    || (d.contact && sanitize(d.contact.nom).indexOf(self.currentTextFilter) !== -1)
                    || (d.client && sanitize(d.client.nom).indexOf(self.currentTextFilter) !== -1);
            });
        }
        this.rows = tempData;
        // Whenever the filter changes, always go back to the first page
        this.table.offset = 0;
    }



    onSelect(event) {
        var selected: Array<Prospection> = event.selected;
        if (selected.length > 0) {
            this.router.navigate(['/prospections/' + selected[0]._id]);
        }
    }



}



