import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {ReactiveFormsModule} from '@angular/forms';
import {AppComponent} from './app.component';
import {PouchdbService} from './services/pouchdb-service/pouchdb.service';
import {AuthService} from './services/auth/auth.service';
import {AuthGuard} from './services/auth/auth.guard';
import {HomeComponent} from './home/home.component';
import {LoginComponent} from './login/login.component';
import {AppRoutingModule} from './app-routing.module';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {OrganismesComponent} from './organisme/list/organismes.component';
import {OrganismeComponent} from './organisme/edit/organisme.component';
import {ContactsComponent} from './contact/list/contacts.component';
import {ContactComponent} from './contact/edit/contact.component';
import {NgxDatatableModule} from '@swimlane/ngx-datatable';
import {WidgetsModule} from './widgets/widgets.module';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {AgmCoreModule} from '@agm/core';

import {GooglePlaceService} from './services/google-place-service/google-place.service';
import {GooglePlaceDirective} from './directives/google-place.directive';
import {OrganismeCardComponent} from './organisme/card/organisme-card.component';
import { ParametresComponent } from './parametres/list/parametres.component';
import { CapitalizeCamelPipe } from './pipes/capitalize-camel.pipe';
import { ParametreComponent } from './parametres/edit/parametre.component';
import { LiensOrganismePipe } from './pipes/liens-organisme.pipe';
import { LiensOrganismeVillePipe } from './pipes/liens-organisme-ville.pipe';
import { MissionsComponent } from './mission/list/missions.component';
import { MissionComponent } from './mission/edit/mission.component';
import { CollaborateurComponent } from './collaborateur/edit/collaborateur.component';
import { CollaborateursComponent } from './collaborateur/list/collaborateurs.component';
import { CollaborateurCardComponent } from './collaborateur/card/collaborateur-card.component';
import { ProspectionsComponent } from './prospection/list/prospections.component';
import { ProspectionComponent } from './prospection/edit/prospection.component';

@NgModule({
    declarations: [
        AppComponent,
        HomeComponent,
        LoginComponent,
        OrganismesComponent,
        OrganismeComponent,
        ContactComponent,
        ContactsComponent,
        GooglePlaceDirective,
        OrganismeCardComponent,
        ParametresComponent,
        CapitalizeCamelPipe,
        ParametreComponent,
        LiensOrganismePipe,
        LiensOrganismeVillePipe,
        MissionsComponent,
        MissionComponent,
        CollaborateurComponent,
        CollaborateursComponent,
        CollaborateurCardComponent,
        ProspectionsComponent,
        ProspectionComponent,
    ],
    imports: [
        BrowserModule,
        ReactiveFormsModule,
        FormsModule,
        HttpClientModule,
        AppRoutingModule,
        NgbModule,
        NgxDatatableModule,
        BrowserAnimationsModule,
        WidgetsModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyByAVVURZudjBg5ncvzbjlDOqNz02ZwIw0',
            libraries: ["places"]
        })
    ],
    providers: [
        PouchdbService,
        AuthService,
        AuthGuard,
        GooglePlaceService
    ],
    bootstrap: [AppComponent]
})
export class AppModule {}



