import {Component, Input} from '@angular/core';
import {Collaborateur} from '../../model';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import {DomSanitizer, SafeUrl} from '@angular/platform-browser';
import {Router} from '@angular/router';


@Component({
    selector: 'app-collaborateur-card',
    templateUrl: './collaborateur-card.component.html',
    styleUrls: ['./collaborateur-card.component.scss']
})
export class CollaborateurCardComponent {
    _collaborateur: Collaborateur = new Collaborateur();
    image: SafeUrl;

    @Input() set collaborateur(value: Collaborateur) {
        this._collaborateur = value;
        this.loadImage();

    }

    constructor(private pouchdbservice: PouchdbService, private sanitizer: DomSanitizer, private router: Router) {}

    loadImage() {
        if (this._collaborateur && this._collaborateur._id) {
            this.pouchdbservice.getImage(this._collaborateur._id).subscribe(
                response => {
                    if (response) {
                        this.image = this.sanitizer.bypassSecurityTrustResourceUrl(response);
                    }
                },
                err => {
                    this.image="assets/images/noavatar.png"
                    console.log(err);
                }
            );
        }
    }
    
    displayCollaborateur() {
        this.router.navigate(['/collaborateurs/' + this._collaborateur._id]);
    }

}



