import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {HomeComponent} from './home/home.component';
import {LoginComponent} from './login/login.component';
import {OrganismesComponent} from './organisme/list/organismes.component';
import {OrganismeComponent} from './organisme/edit/organisme.component';
import {ContactsComponent} from './contact/list/contacts.component';
import {ContactComponent} from './contact/edit/contact.component';
import {MissionsComponent} from './mission/list/missions.component';
import {MissionComponent} from './mission/edit/mission.component';
import {AuthGuard} from './services/auth/auth.guard';
import {ParametresComponent} from './parametres/list/parametres.component';
import {CollaborateurComponent} from './collaborateur/edit/collaborateur.component';
import {CollaborateursComponent} from './collaborateur/list/collaborateurs.component';
import {ProspectionsComponent} from './prospection/list/prospections.component';
import {ProspectionComponent} from './prospection/edit/prospection.component';

const routes: Routes = [
    {path: '', component: HomeComponent, canActivate: [AuthGuard]},
    {path: 'organismes', component: OrganismesComponent, canActivate: [AuthGuard]},
    {path: 'organismes/:id', component: OrganismeComponent, canActivate: [AuthGuard]},
    {path: 'contacts', component: ContactsComponent, canActivate: [AuthGuard]},
    {path: 'contacts/:id', component: ContactComponent, canActivate: [AuthGuard]},
    {path: 'missions', component: MissionsComponent, canActivate: [AuthGuard]},
    {path: 'missions/:id', component: MissionComponent, canActivate: [AuthGuard]},
    {path: 'collaborateurs', component: CollaborateursComponent, canActivate: [AuthGuard]},
    {path: 'collaborateurs/:id', component: CollaborateurComponent, canActivate: [AuthGuard]},
    {path: 'prospections', component: ProspectionsComponent, canActivate: [AuthGuard]},
    {path: 'prospections/:id', component: ProspectionComponent, canActivate: [AuthGuard]},
    {path: 'parametres', component: ParametresComponent, canActivate: [AuthGuard]},
    {path: 'login', component: LoginComponent}
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {}


