import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import {FormGroup, FormBuilder, Validators, FormArray} from '@angular/forms';
import {Parametres} from '../../model';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';

@Component({
    selector: 'app-parametre',
    templateUrl: './parametre.component.html',
    styleUrls: ['./parametre.component.scss']
})
export class ParametreComponent implements OnInit {

    form: FormGroup;
    errorMessage: String;
    private formSubmitAttempt: boolean = false;
    private _editParameterName: string;
    values: Array<string> = [];
    items: FormArray;

    @Input() parametres: Parametres;
    @Output() updateDone: EventEmitter<string> = new EventEmitter();
    @Input() set editParameterName(value: string) {
        this._editParameterName = value;
        this.form.reset()
        if (this._editParameterName) {
            this.values = this.parametres.listesDeChoix[this._editParameterName];
            for(value of this.values){
                this.items.push(this.createItem(value));
            }
        }
    }

    constructor(private fb: FormBuilder, private pouchdbservice: PouchdbService) {
        this.form = this.fb.group({
            items: this.fb.array([])
        });
        this.items = this.form.get('items') as FormArray;
    }

    ngOnInit() {

    }

    createItem(value:string): FormGroup {
        return this.fb.group({
            item: value,
        });
    }

    addItem() {
        this.items.push(this.createItem(''));
    }

    removeItem(index: number) {
        (<FormArray> this.form.get('items')).removeAt(index);
    }



    onSubmit() {
        if (this.form.valid) {
            var items = new Array<string>();
            for(let item of this.form.value.items){
                items.push(item.item);
            }
            this.parametres.listesDeChoix[this._editParameterName] = items.sort();

            this.pouchdbservice.save(this.parametres).subscribe(
                response => {
                    this.updateDone.emit("saved");
                    console.log(response);
                },
                err => {
                    this.errorMessage = err;
                }
            );

        }
        this.formSubmitAttempt = true;
    }

}



